import React, { Component } from "react";
import { Map, Marker } from "react-amap";
import { useState } from "react";
import { Box } from "@chakra-ui/react";

function Address() {
  const [mapState, setMapState] = useState({
    mapZoom: 15, // zoom
    //https://lbs.amap.com/api/javascript-api/guide/abc/preparekey
    mapKey: "1234567809843asadasd", //Key
    status: {
      zoomEnable: true,
      dragEnable: false,
    },
    mapCenter: [113.933334, 22.63607], //
    mapMake: [113.933334, 22.63607], //marker
  });

  return (
    <Box w="100%" h="270px">
      <Map
        amapkey={mapState.mapKey}
        center={mapState.mapCenter}
        zoom={mapState.mapZoom}
        status={mapState.status}
      >
        {/*marker position*/}
        <Marker
          position={mapState.mapMake}
          label={{
            // 
            //   offset: new AMap.Pixel(20, 20),
            // 
            content: "<div ></div>",
            // 
            direction: "top",
          }}
        />
      </Map>
    </Box>
  );
}

export default Address;
